.boton, .button, button {
  display: inline-block;
  line-height: 2.5em;
  background: #ddd;
  color: #333;
  padding: 0 1.5em;
  cursor: pointer;
  margin-bottom: 1em; }

.boton:hover, .button:hover, button:hover {
  background: #d0d0d0; }

.boton:active, .button:active, button:active {
  transform: scale(0.97); }

.boton.round, .button.round, button.round {
  display: inline-block;
  line-height: 2.5em;
  background: #ddd;
  color: #333;
  padding: 0 1.5em;
  cursor: pointer;
  margin-bottom: 1em;
  border-radius: 1.25em; }

.boton.round:hover, .button.round:hover, button.round:hover {
  background: #d0d0d0; }

.boton.round:active, .button.round:active, button.round:active {
  transform: scale(0.97); }

.boton.radius, .button.radius, button.radius {
  display: inline-block;
  line-height: 2.5em;
  background: #ddd;
  color: #333;
  padding: 0 1.5em;
  cursor: pointer;
  margin-bottom: 1em;
  border-radius: 1.25em; }

.boton.radius:hover, .button.radius:hover, button.radius:hover {
  background: #d0d0d0; }

.boton.radius:active, .button.radius:active, button.radius:active {
  transform: scale(0.97); }

* {
  box-sizing: border-box; }

body {
  color: #2E0927;
  display: flex;
  font-family: "Oxygen";
  font-size: 18px; }

form {
  margin: 0; }

h1 {
  color: #FF2D00;
  font-family: "Oswald";
  font-size: 2em;
  text-align: center; }

input {
  display: block;
  margin: 0.5em 0;
  padding: 5px;
  width: 100%; }

label {
  display: blocK; }

textarea {
  height: 20em;
  max-width: 100%;
  padding: 5px;
  width: 100%; }

.pages__item {
  border-bottom: 1px solid #04756F; }
  .pages__item a {
    cursor: pointer;
    padding: 5px;
    display: block;
    transition: all .4s;
    width: 100%; }
    .pages__item a.active, .pages__item a:active, .pages__item a:hover {
      background-color: #04756F;
      color: #fff;
      padding-left: 20px; }

.alert {
  border-style: solid;
  border-width: 1px;
  padding: 0 20px;
  text-align: center; }
  .alert--danger {
    background-color: #D90000;
    border: 1px solid #FF2D00;
    color: #fff; }

.btn {
  align-items: center;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  line-height: 2;
  margin: 0;
  padding: 0 20px; }
  .btn--primary {
    background-color: #04756F; }
    .btn--primary:hover, .btn--primary:active, .btn--primary.active {
      background-color: #024440; }
  .btn--warning {
    background-color: #FF8C00; }
    .btn--warning:hover, .btn--warning:active, .btn--warning.active {
      background-color: #cc7000; }

.ed-container {
  max-width: 768px; }
  @media all and (min-width: 768px) {
    .ed-container {
      border: 1px solid #04756F;
      margin-top: 30px;
      padding: 15px 0; } }

.text-center {
  text-align: center; }

.page-list .alert {
  margin-bottom: 2em; }

.page-edit .btn--primary,
.page-edit .btn--warning {
  width: 100%;
  margin-top: 0.5em; }

.page-view .buttons {
  margin-top: 2em; }

.page-view .btn--primary,
.page-view .btn--warning {
  width: 100%;
  margin-top: 0.5em; }
